* {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  padding: 0;
  box-sizing: border-box;
}
.App {
  width: 1150px;
  margin-inline: auto;
  padding-inline: 10px;
}
@media (max-width: 1200px) {
  .App {
    width: 900px;
  }
}
@media (max-width: 950px) {
  .App {
    width: 700px;
  }
}
@media (max-width: 750px) {
  .App {
    width: 100%;
  }
}
.inputMession {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  padding-top: 10px;
}
.inputMession input {
  width: 80%;
  border: none;
  outline: none;

  border-radius: 5px;
  padding: 5px 10px;
  font-size: 1.1rem;
  position: relative;
  transition: 0.5s;
  border: 1px solid rgb(72, 185, 255);
}
.inputMession input:focus,
.inputMession button:hover {
  box-shadow: 5px 3px 10px rgb(72, 185, 255);
}
.inputMession button {
  padding: 10px 20px;
  font-size: 1.1rem;
  transition: 0.5s;
  background-color: rgb(72, 185, 255);
  border: none;
  border-radius: 5px;
  color: white;
}
.undone h2 {
  color: rgb(50, 50, 50);
}
.done h2 {
  color: rgb(72, 185, 255);
}
h2 {
  text-align: center;
  padding-block: 5px;
}
.undone {
  border: 1px solid rgb(50 50 50);
  border-radius: 5px;
  margin-block: 10px;
  color: rgb(50 50 50);
}
.done {
  border: 1px solid rgb(72, 185, 255);
  border-radius: 5px;
  color: rgb(72, 185, 255);
}
.item {
  font-size: 1.2rem;
  padding-inline: 20px;
  padding-bottom: 5px;
}
.item::before {
  content: attr(data-number) "-";
}
.done .item {
  text-decoration: line-through;
}
